import React, {
	useEffect,
	useState,
} from "react";

import "./CheckBox.css";

const CheckBox = ({ data, active, updSelect }) => {
	
    return (
        <div className="_dsplFlx main_chckbx ">
           {data?.map(option=>{
			   return(
				   <div key={option.id} className="item_chckbx " is-active={`${active===option.id}`} onClick={()=>updSelect(option.id)}>
					   {option.label}
				   </div>
			   )
		   })}
        </div>
    )
}


export default CheckBox;